import React from 'react'
import steve from '../../images/steves.png'
import classnames from 'classnames'



class ContentBlock extends React.Component{
	constructor(){
		super();
		this.state = {time:0}
	}
	runTimer(){
		console.log(this.state.time +'testing123');
		this.setState({time: this.state.time + 100});
		let timer = setInterval(()=>{this.setState({time: this.state.time + 100})},100)
		setTimeout(()=>{
			clearInterval(timer); 
			console.log('cleared'); 
			this.setState({time:30000})},6000);
	}
	getClass(id, addend, defaultClass, delay){ 
		let node = document.getElementById(id);

		if(!node ){
			return classnames(defaultClass, 'clearfix', 'show');
		}
		if(delay&&delay>this.state.time){
			return classnames(defaultClass, 'hidden', 'clearfix');
		}

		let show = 'show'+ addend;
		let hide = 'hide';
		let top = node.getBoundingClientRect().top;
		
		if(top<window.innerHeight*.75 && defaultClass==='content-title' && this.state.time ==0){
			this.runTimer();
		}
		if(top>window.innerHeight && this.state.time>0 && defaultClass==='content-title'){
			this.setState({time:0})
		}

		
		return classnames(defaultClass, 'clearfix', {[show]:  top<window.innerHeight*.75},{[hide]: top>window.innerHeight*.75} );

	}
	componentDidMount(){

	}
	render(){
		let data = this.props.data;
		let class2 = this.props.back? 'content-left': 'content-right' 
		let class1 = this.props.back? 'content-right': 'content-left' 
		return (
			<div  className='content-container' id={data.title + '1'}>
				<div className={this.getClass(data.title + '-container', '', 'content-title')} id={data.title}>{data.title}</div>
				<div className={"clearfix "+class1}>
					<div className={this.getClass(data.title + '-container', '1', 'pic-div')} style={{backgroundImage: 'url(../../images/profback.png)'}}>
						<img className="pic" src={steve}/>
					</div>
					<div className='list-stuff'>
						<ul>
							{data.list.map((x,i)=>(
								<li 
									className={this.getClass(data.title + '-container', '2', 'fact-li', 200*i)}
									style={{left:100*i}}
									>{x}</li>
								))}
						</ul>
					</div>
				</div>
				<br/>
				<div className={"clearfix "+class2}>
					<div className={this.getClass(data.title + '-container', '3', "content-description")}>
						{data.description}	
					</div>
				</div>
			</div>
		)
	}
}

export default ContentBlock;


