import React from 'react'
import steve from '../../images/steves.png'

const FeaturedWork = ({data}) =>(
	<div className="featured-work-container" >
		<div className="featured-inner-container" id="featured-work">
			<div className="featured-title"><h1>{data.title}</h1></div>
			<div className="featured-left">
			<div className="featured-image">
				<img className="pic" src={steve}/>
			</div>
				<div className="featured-list">
					<ul>
						{data.list.map(x=>(
							<li>{x}</li>
							))}
					</ul>
				</div>
			</div>
			<div className="featured-right">
				<div className="featured-description">{data.description}</div>
			</div>
		</div>
	</div>
	)

export default FeaturedWork;


